:root {
  --body: #ffffff;
  --black-color: #ffffff;
  --opacity: none;
  --text: #000000;
  --text-color: #000000;
  --box-shadow: rgba(44, 44, 44, 0.4);
  --theme-code: #3b3f41;
  --black-text: #ffffff;
  --text-light: hsla(120, 77%, 38%, 1);
  --text-danger: hsla(120, 77%, 38%, 1);
  --text-dark: #000000;
  --border-color: rgba(217, 217, 217, 0.87);
}

[data-theme="dark"] {
  --body: #121212;
  --black-color: #202124;
  --opacity: 0.45;
  --text: #ffffff;
  --text-color: #dadada;
  --box-shadow: rgba(237, 237, 237, 0.4);
  --theme-code: #3b3f41;
  --black-text: #000000;
  --text-light: #ffffff;
  --text-danger: #ffffff;
  --text-dark: #999999;
  --border-color: #121212;
}
