/*--------------------------------

LineIconsPro Regular Web Font
Author: lineicons.com

-------------------------------- */
@font-face {
  font-family: 'LineIconsPro Regular';
  src: url('../fonts/LineIconsPro Regular.eot');
  src: url('../fonts/LineIconsPro Regular.eot') format('embedded-opentype'), url('../fonts/LineIconsPro Regular.woff2') format('woff2'), url('../fonts/LineIconsPro Regular.woff') format('woff'), url('../fonts/LineIconsPro Regular.ttf') format('truetype'), url('../fonts/LineIconsPro Regular.svg') format('svg');
  font-weight: normal;
  font-style: normal;
}
/*------------------------
	base class definition
-------------------------*/
.lnir {
  display: inline-block;
  font: normal normal normal 1em/1 'LineIconsPro Regular';
  speak: none;
  text-transform: none;
  /* Better Font Rendering */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
/*------------------------
  change icon size
-------------------------*/
/* relative units */
.lnir-sm {
  font-size: 0.8em;
}
.lnir-lg {
  font-size: 1.2em;
}
/* absolute units */
.lnir-16 {
  font-size: 16px;
}
.lnir-32 {
  font-size: 32px;
}
/*----------------------------------
  add a square/circle background
-----------------------------------*/
.lnir-bg-square,
.lnir-bg-circle {
  padding: 0.35em;
  background-color: #eee;
}
.lnir-bg-circle {
  border-radius: 50%;
}
/*------------------------------------
  use icons as list item markers
-------------------------------------*/
.lnir-ul {
  padding-left: 0;
  list-style-type: none;
}
.lnir-ul > li {
  display: flex;
  align-items: flex-start;
  line-height: 1.4;
}
.lnir-ul > li > .lnir {
  margin-right: 0.4em;
  line-height: inherit;
}
/*------------------------
  spinning icons
-------------------------*/
.lnir-is-spinning {
  -webkit-animation: lnir-spin 2s infinite linear;
  -moz-animation: lnir-spin 2s infinite linear;
  animation: lnir-spin 2s infinite linear;
}
@-webkit-keyframes lnir-spin {
  0% {
    -webkit-transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
  }
}
@-moz-keyframes lnir-spin {
  0% {
    -moz-transform: rotate(0deg);
  }
  100% {
    -moz-transform: rotate(360deg);
  }
}
@keyframes lnir-spin {
  0% {
    -webkit-transform: rotate(0deg);
    -moz-transform: rotate(0deg);
    -ms-transform: rotate(0deg);
    -o-transform: rotate(0deg);
    transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
    -moz-transform: rotate(360deg);
    -ms-transform: rotate(360deg);
    -o-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}
/*------------------------
  rotated/flipped icons
-------------------------*/
.lnir-rotate-90 {
  filter: progid:DXImageTransform.Microsoft.BasicImage(rotation=1);
  -webkit-transform: rotate(90deg);
  -moz-transform: rotate(90deg);
  -ms-transform: rotate(90deg);
  -o-transform: rotate(90deg);
  transform: rotate(90deg);
}
.lnir-rotate-180 {
  filter: progid:DXImageTransform.Microsoft.BasicImage(rotation=2);
  -webkit-transform: rotate(180deg);
  -moz-transform: rotate(180deg);
  -ms-transform: rotate(180deg);
  -o-transform: rotate(180deg);
  transform: rotate(180deg);
}
.lnir-rotate-270 {
  filter: progid:DXImageTransform.Microsoft.BasicImage(rotation=3);
  -webkit-transform: rotate(270deg);
  -moz-transform: rotate(270deg);
  -ms-transform: rotate(270deg);
  -o-transform: rotate(270deg);
  transform: rotate(270deg);
}
.lnir-flip-y {
  filter: progid:DXImageTransform.Microsoft.BasicImage(rotation=0);
  -webkit-transform: scale(-1, 1);
  -moz-transform: scale(-1, 1);
  -ms-transform: scale(-1, 1);
  -o-transform: scale(-1, 1);
  transform: scale(-1, 1);
}
.lnir-flip-x {
  filter: progid:DXImageTransform.Microsoft.BasicImage(rotation=2);
  -webkit-transform: scale(1, -1);
  -moz-transform: scale(1, -1);
  -ms-transform: scale(1, -1);
  -o-transform: scale(1, -1);
  transform: scale(1, -1);
}
